@import "mixins";

.cmswt {
  &-InstantSearchPopup {
    box-sizing: border-box;
    z-index: -300;
    opacity: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(0, 0, 0, 0.75);
    top: 0;
    left: 0;
    transition: opacity .1s ease-in;
    overflow-y: auto;
    @include respond(tablet) {
      padding: 3em;
    }

    &--active {
      z-index: 999999;
      opacity: 1;
    }

    &--results {
      box-sizing: inherit;
      position: relative;
      background: #fff;
      margin: 0 auto;
      width: 100%;
      padding: 4em 2em 2em 2em;
      @include respond(tablet) {
        max-width: 95%;
        border-radius: 10px;
      }
    }

    &--closeIcon {
      position: absolute;
      top: 1em;
      right: 2em;
      color: #000;
      width: 2rem;
      height: 2rem;
      z-index: 10;
    }
  }
}

a.cm-swt-instant-search-popup--trigger-default {
  color: #000;
  text-decoration: none;
  oultine: none;

  svg {
    width: 20px;
  }
}