@import "mixins";
//global styles inherited by other addons as well
.ais {
  &-Hits {
    ul, ol {
      margin: 0;
      padding: 0;
    }
  }

  &-Pagination {
    ul, ol {
      list-style: none;
    }

    &-list {
      flex-wrap: wrap;
      gap: 6px;
    }
  }

  //menu widget - basically a list of facets
  &-Menu {
    &-showMore--disabled {
      display: none;
    }
  }

  //refinement widget - checkbox list of facets
  &-RefinementList {
    &-showMore--disabled {
      display: none;
    }
  }

  &-SearchBox {
    width: 100%;

    &-submit {
      display: none;
    }
  }

  &-Panel {
    @include respond(mobile-landscape) {
      margin-bottom: 25px;
    }
    margin-bottom: 25px;

    ul, ol {
      margin: 0;
      padding: 0;
      list-style: none;
    }

    //panel collapse icon
    &-collapseIcon {
      color: #000000;
      @include respond(tablet) {
        display: none;
      }
    }

    &-collapseButton {
      border: none;
      background-color: inherit;
    }
  }

  /*Current Refinements*/
  &-CurrentRefinements {
    &-list {
      padding: 0;
      margin: 0;
    }

    &-item {
      padding: 0.3rem 0.5rem;
    }
  }

  /*Clear refinements*/
  &-ClearRefinements {
    height: 100%;

    &-button {
      height: 100%;
    }
  }

  &-ClearRefinements-button,
  &-CurrentRefinements-reset,
  &-GeoSearch-redo,
  &-GeoSearch-reset,
  &-HierarchicalMenu-showMore,
  &-InfiniteHits-loadMore,
  &-InfiniteHits-loadPrevious,
  &-InfiniteResults-loadMore,
  &-Menu-showMore,
  &-RefinementList-showMore {
    padding: 0.5em 0.7em;
  }

  &-InfiniteHits-loadMore {
    &--disabled {
      display: none;
    }
  }
}