@import "mixins";

.cmswt {
  //Main Instance & AIS overrides
  &-InstantSearch {
    box-sizing: border-box;

    * {
      box-sizing: inherit;
    }

    ul, ol {
      padding: 0;
      margin: 0;
    }

    @include respond(tablet) {
      display: flex;
      flex-wrap: wrap;
      gap: 1em;
      margin-bottom: 1em;
    }
    //add here since we need a wrapper
    .ais-SearchBox {
      width: 100%;

      &-input {
        border-radius: 5px;
        background: none;
        border: 1px solid #c4c8d8;
        padding: 0.85em 0.6em;

        &::placeholder {
          color: #000;
        }
      }

      &-submit {
        display: none;
      }
    }

    /*Sort By*/
    .ais-SortBy {
      &-select {
        width: 100%;
        //exception case
        @include respond(mobile) {
          padding: 0.85em 0.6em;
          background-position: 97% 50%;
        }
        @include respond(tablet) {
          width: auto;
        }
      }
    }

    /*Panel*/
    .ais-Panel {
      //panel collapse icon
      &-collapseIcon {
        color: #000000;
        @include respond(tablet) {
          display: none;
        }
      }

      &-collapseButton {
        border: none;
        background-color: inherit;
      }
    }

    /*RefinementList*/
    .ais-RefinementList {
      ul, ol {
        padding: 0;
      }

      &-searchBox {
        .ais-SearchBox-submit {
          display: none;
        }
      }

      &-label {
        display: flex;
        gap: 0.35em;
        row-gap: 0.5em;
        flex-wrap: wrap;
        align-items: center;
      }
    }

    /*Pagination*/
    .ais-Pagination {
      &-link {
        padding: 0.1em 0.6em;
        text-decoration: none;
      }

      &--noRefinement {
        display: none;
      }
    }

    a.ais-Pagination-link {
      text-decoration: none;
    }

    /*Hit List*/
    .ais-Hits-list,
    .ais-InfiniteHits-list {
      list-style: none;
      display: grid;
      margin: 0;
      gap: 1rem;
      padding: 0;
    }

    /*Hit Item*/
    .ais-Hits-item,
    .ais-InfiniteHits-item {
      padding: 0;
      width: 100%;
      border: none;
      box-shadow: 0 0 1px 1px #ddd;
      margin: 0;

      .hit-header--link {
        width: 100%;
      }

      .hit-description {
        height: auto;
        max-height: 99px;
        text-overflow: ellipsis;
        overflow: hidden;
        padding-top: 1rem;
        line-height: 1.25rem;
      }

      .hit-link {
        margin-top: 1rem;
      }
    }

    .ais-InfiniteHits--empty,
    .ais-Hits--empty {
      min-height: 100px;
      position: relative;
    }

    .rheostat-tooltip {
      word-break: normal;
    }

    .cmswt-noSearchResults {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  //Search Box
  &-SearchBox {
    width: 100%;
  }

  //Search Header
  &-Header {
    width: 100%;
    display: flex;
    gap: 1em;
    flex-wrap: wrap;
    @include respond(mobile) {
      gap: 0.5em;
    }
  }

  //Sub Header
  &-SubHeader {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 1em auto;
    flex-wrap: wrap;
    @include respond(mobile) {
      margin: 0 0 0.5em 0;
    }
    @include respond(mobile_landscape) {
      margin: 0;
    }
  }

  &-CollectionMenu {
    width: 100%;
    @include respond(mobile_landscape) {
      width: auto;
    }

    &--singleSource {
      .cmswt-CollectionMenu-current {
        display: block;
        border: none;
        padding-left: 0;

        svg {
          display: none;
        }
      }

      .cmswt-IndexSwitcher {
        display: none;
      }
    }
  }

  &-CollectionMenu-current {
    display: block;
    border: 1px solid #c4c8d8;
    border-radius: 5px;
    padding: 0.5em;
    width: 100%;
    position: relative;
    margin-bottom: 0.5em;
    @include respond(mobile_landscape) {
      display: none;
    }

    svg {
      width: 20px;
      height: 20px;
      position: absolute;
      transform: translateY(-50%);
      top: 50%;
      right: 0.25em;
    }

    &--listOpen {
      margin-bottom: 0;
      border-radius: 5px 5px 0 0;
      border-bottom: none;
    }
  }

  //Index Switcher
  &-IndexSwitcher {
    display: none;
    margin: 1rem 0;
    list-style: none;
    flex-wrap: wrap;
    border: 1px solid #c4c8d8;
    border-radius: 5px;

    //another exception may we should write a max-width respond as well 
    @include respond(mobile) {
      .cmswt-InstantSearch & {
        margin-bottom: 0.5em;
      }
    }
    @include respond(mobile_landscape) {
      display: flex;
      border-color: #eaeef3;
    }

    &--block {
      display: block;
      border-top: 0;
      border-radius: 0 0 5px 5px;
    }

    &.hide {
      display: none;
    }

    .cmswt-InstantSearch &-item {
      margin: 0;

      .cmswt-IndexSwitcher-link {
        text-decoration: none;
        color: #000;
        padding: 0.5em 1.5em;
        display: inline-block;
        width: 100%;

        &:focus {
          border: none;
          outline: none;
        }
      }


      &:hover,
      &.active {
        background: #eaeef3;
      }
    }
  }

  //Sorting Container
  &-Sort {
    width: 100%;
    @include respond(mobile_landscape) {
      width: auto;
    }
  }

  //Stats
  &-Stats {
    display: none;
    @include respond(tablet) {
      display: block;
      width: 100%;
    }
  }

  //Refinements (current refinements and clear refinements )
  &-Refinements {
    display: none;
    @include respond(tablet) {
      display: flex;
      flex-wrap: wrap;
    }

    .ais-ClearRefinements-button--disabled {
      display: none
    }
  }

  //main panel contains search-result-wrapper
  &-MainPanel {
    @include respond(tablet) {
      width: calc(80% - 1em);
    }

    &--fullWidth {
      width: 100%;
    }
  }

  //Results 
  &-Results {
    .hit-cats {
      display: flex;
      gap: 0.5rem;
      flex-wrap: wrap;
      font-size: 0.8rem;
    }

    .hit-content {
      padding: 1rem;
    }

    .hit-meta {
      line-height: 0.8rem;
    }

    .posted-on {
      font-size: 0.8rem;
      display: inline;
      line-height: 1.5;
    }

    img {
      max-width: 100%;
      width: 100%;
      height: 180px;
      object-fit: cover;
    }

    .cm-col-4 {
      img {
        height: 140px;
      }
    }

    .cm-col-5 {
      img {
        height: 140px;
      }
    }

    @include respond(mobile_landscape) {
      .cm-col-1 {
        .ais-Hits-item,
        .ais-InfiniteHits-item {
          display: flex;
        }
      }
    }
    //column design
    @include respond(tablet) {
      .cm-col-1 {
        .ais-Hits-item,
        .ais-InfiniteHits-item {
          box-shadow: none;
          align-items: center;

          .hit-header {
            min-width: 180px;

            img {
              max-width: 180px;
              height: auto;
            }
          }

          .hit-content {
            padding-top: 0;
          }
        }
      }

      .cm-col-2 {
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }

      .cm-col-3 {
        grid-template-columns: repeat(3, minmax(0, 1fr));
      }

      .cm-col-4 {
        grid-template-columns: repeat(4, minmax(0, 1fr));
      }

      .cm-col-5 {
        grid-template-columns: repeat(5, minmax(0, 1fr));
      }
    }
  }

  //Result
  &-Result {

    &.active {
      display: block
    }
  }

  //Filter Panel
  &-FilterPanel {
    position: relative;

    &--hidden {
      display: none;
    }

    @include respond(mobile) {
      margin-bottom: 1.5em;
    }
    @include respond(tablet) {
      width: 20%;
    }

    &-toggle {
      border: 1px solid #c4c8d8;
      border-radius: 5px;
      padding: 0.5em;
      position: relative;
      margin: 0 0 0.5em 0;
      @include respond(mobile_landscape) {
        display: none;
      }
    }

    &-toggleIcon {
      position: absolute;
      transform: translateY(-50%);
      top: 50%;
      right: 0.25em;
      width: 20px;
      height: 20px;
    }

    &-items {
      display: none;
      @include respond(mobile_landscape) {
        display: block;
      }

      &--show {
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        background: #fff;
        z-index: 999999;
        min-height: 100vh;
        padding: 0;
        display: block;
      }
    }


    &-itemsPopupHeader {
      position: relative;
      padding: 0 1.5rem;
      margin: 1.5rem auto;
      @include respond(mobile_landscape) {
        display: none;
      }
    }

    &-itemsPopupLabel {
      position: relative;
    }

    &-itemsPopupHeaderCloseLogo {
      width: 24px;
      height: 24px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -5px;
    }

    &-itemsContent {
      @include respond(mobile) {
        padding: 0 1.5rem;
        height: 75vh;
        overflow-y: scroll;
      }
    }

    &-itemsFooter {
      bottom: 0;
      left: 0;
      padding: 1.5rem;
      position: absolute;
      width: 100%;
      @include respond(mobile_landscape) {
        display: none;
      }
    }

    a.cmswt-FilterPanel-itemsFooterCloseLink {
      background: #0096db;
      border-radius: 5px;
      color: #fff;
      display: inline-block;
      font-weight: bold;
      padding: 1rem;
      text-align: center;
      text-decoration: none;
      text-transform: uppercase;
      width: 100%;
    }
  }

  //Pagination
  &-Pagination {
    margin-top: 1em;
  }
}